:root {
  --accent-color: #76729e;
  --slider-height: 8px;
}

input[type="checkbox"] {
  appearance: checkbox;
  border: 2px solid red !important;
  margin: 0;
  box-shadow: none;
  filter: grayscale(1);
}

input[type="range"] {
  -webkit-appearance: none;
  border-radius: var(--slider-height);
  outline: none;
  padding: 0;
  margin: 0;
  height: var(--slider-height);
  min-width: 400px;
  background-color: white;
  overflow: hidden;
}

input[type="range"]::-webkit-slider-thumb {
  width: calc(var(--slider-height) * 2);
  height: var(--slider-height);
  -webkit-appearance: none;
  cursor: ew-resize;
  background: #232323;
  box-shadow: -999px 0 0 999px var(--accent-color);
}

.control,
.range-slider {
  display: flex;
  align-items: center;
  margin: 2px 0;
}

.control__title {
  min-width: 65px;
  font-size: 9pt;
  margin-right: 0.5rem;
  font-weight: bold;
}

.control__details,
.range-slider__value {
  margin-left: 0.5rem;
  font-size: 9pt;
}
